import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { JabatanRoutingModule } from './jabatan-routing.module';
import { ListComponent } from './list/list.component';
import { EditComponent } from './edit/edit.component';
import { AddComponent } from './add/add.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { NgZorroAntdModule } from 'ng-zorro-antd';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { DetailComponent } from './detail/detail.component';
import { DocumentModule } from 'src/app/components/document/document.module';
import { PipeModule } from 'src/app/pipe/pipe.module';


@NgModule({
    declarations: [ListComponent, EditComponent, AddComponent, DetailComponent],
    imports: [
        CommonModule,
        JabatanRoutingModule,
        NgZorroAntdModule,
        ReactiveFormsModule,
        FormsModule,
        DocumentModule,
        PipeModule
    ],
    providers: [
        FormBuilderTypeSafe
    ]
})
export class JabatanModule { }
