import { Component, OnInit } from '@angular/core';
import { Jabatan, Departemen, Divisi } from 'src/app/sdk/hr/models';
import { CompanyJabatanService, CompanyDivisiService, CompanyDepartemenService, DirektoriVPegawaiPosisiService } from 'src/app/sdk/hr/services';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { standarFlow } from 'src/app/components/approval/standar/standar-approval.service';
import { SingleUploadService } from 'src/app/components/document/single-upload.service';
import { FormGroupTypeSafe, FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { FormControl, Validators } from '@angular/forms';
import { PesanService } from 'src/app/services/tool/pesan.service';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private routerActive: ActivatedRoute,
        private pesan: PesanService,
        private fb: FormBuilderTypeSafe,
        private companyJabatanService: CompanyJabatanService,
        private companyDivisiService: CompanyDivisiService,
        private companyDepartemenService: CompanyDepartemenService,
        private direktoriVPegawaiPosisiService: DirektoriVPegawaiPosisiService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private singleUploadService: SingleUploadService,
    ) { }

    toogleUpload = false

    dataJabatan: Jabatan = {}
    dataDepartemen: Departemen = {}
    dataDivisi: Divisi = {}


    id: string = ''

    ngOnInit() {
        this.id = this.routerActive.snapshot.paramMap.get('id')
        console.log(this.id)
        this.getJabatan();
    }

    togleShowUpload() {

        this.toogleUpload = true;
    }

    params: standarFlow = null;
    id_data_flow: string = ''
    getJabatan() {
        this.spinner.show()
        this.companyJabatanService.getCompanyJabatansId(this.id).subscribe(
            response => {
                this.dataJabatan = response.data;
                this.getDivisi(this.dataJabatan.id_divisi)
                this.getDepartemen(this.dataJabatan.id_departemen)
                this.getTotalPegawai(response.data.id_jabatan)
                    this.spinner.hide()



            }
        )

    }

    getDivisi(id_divisi) {
        this.companyDivisiService.getCompanyDivisisId(id_divisi).subscribe(
            data=> {
                this.dataDivisi= data.data
            }
        )
    }
    getDepartemen(id_departemen) {
        this.companyDepartemenService.getCompanyDepartemensId(id_departemen).subscribe(
            data=> {
                this.dataDepartemen= data.data
            }
        )
    }

    getTotalPegawai(id_jabatan) {
        let param = {
            id_jabatan: id_jabatan,
            total: true
        }
        this.direktoriVPegawaiPosisiService.getDirektoriVPegawaiPosisis(JSON.stringify(param)).subscribe(
            data => {
                this.dataJabatan['total_pegawai'] = data.data[0]['total']
            }
        )
    }


    goToBack() {

        this.router.navigate(['/hr/company/module/jabatan/list'])
    }


}
