import { Component, OnInit } from '@angular/core';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { CompanyDepartemenService, CompanyDivisiService, CompanyJabatanService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, ActivatedRoute } from '@angular/router';
import { Jabatan, Departemen, Divisi } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';
import { SingleUploadService } from 'src/app/components/document/single-upload.service';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private companyDepartemenService: CompanyDepartemenService,
        private companyDivisiService: CompanyDivisiService,
        private companyJabatanService: CompanyJabatanService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private singleUploadService: SingleUploadService,
    ) { }

    toogleUpload = false

    data: Jabatan = {};
    ngOnInit() {
        this.id = this.activatedRoute.snapshot.paramMap.get('id');
        this.form();
        this.getDepartemen()
        this.getData()
        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'company_jabatan.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ id_dokumen: data })
                    this.simpan()
                }
            }
        )
    }

    togleShowUpload() {

        this.toogleUpload = true;
    }
    id: string = "";
    getData() {
        this.spinner.show()
        this.companyJabatanService.getCompanyJabatansId(this.id).subscribe(
            data => {
                this.dataForm.setValue(data.data)
                this.spinner.hide()

                this.singleUploadService.param.next({
                    for_module: "company_jabatan",
                    for_table: "erp.jabatan",
                    for_module_id: data.data.id_jabatan
                })
            }
        )
    }


    listDepartemen: Departemen[] = [];
    listDivisi: Divisi[] = [];
    listJabatan: Jabatan[] = [];
    getDepartemen() {
        this.companyDepartemenService.getCompanyDepartemens().subscribe(
            data => {
                this.listDepartemen = data.data;
            }
        )
    }



    getDivisi() {
            this.spinner.show()
            this.companyDivisiService.getCompanyDivisis(JSON.stringify(
                {
                    id_departemen: this.dataForm.value.id_departemen,
                })).subscribe(
                    data => {
                        this.spinner.hide();
                        this.listDivisi = data.data;
                    }
                )
    }


    dataForm: FormGroupTypeSafe<Jabatan>;
    form() {
        this.dataForm = this.fb.group<Jabatan>(
            {
                id_jabatan: new FormControl('', [Validators.required]),
                id_departemen: new FormControl('', [Validators.required]),
                nama_jabatan: new FormControl('', [Validators.required]),
                id_divisi: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
                status: new FormControl(''),
                jumlah_pegawai: new FormControl(''),
                id_dokumen: new FormControl(''),
                id_parent_jabatan: new FormControl(''),
                deskripsi: new FormControl('')
            }
        )
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.data = this.dataForm.value;
        this.companyJabatanService.putCompanyJabatansId({
            id: this.id,
            body: this.data
        }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/company/module/jabatan/list'])
    }


}
